import { Col, Form, Row, Tag } from "antd";
import { useCallback, useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Swal from "sweetalert2";
import { Fn } from "../../../../../helper/function";
import { WorkflowRunService } from "../../../../../services/taskService";
import { MyFormItem } from "../../../../core/Input";
import { Box, MyBox } from "../../../../core/MyBox";
import { MyButton } from "../../../../core/MyButton";
import { TextStrong } from "../../../../core/Text";
import { MonacoEditorComponent } from "../../../test/MonacoEditorComponent";

export default function DetailComponent() {
    const projectKey: string | undefined = useParams().projectKey;
    const workflowKey: string | undefined = useParams().workflowKey;
    const key: string | undefined = useParams().key;

    const nav = useNavigate();
    const [isLoading, setIsLoading] = useState(false);
    const [data, setData] = useState([]);
    const [name, setName] = useState();
    const [resDetail, setResDetail] = useState('{}');

    const loadDataDetail = useCallback(() => {
        if (key !== undefined) {
            setIsLoading(true);
            WorkflowRunService.GetDetail(key, projectKey, workflowKey)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        setData(rs.data.details);
                        setName(rs.data.name);
                    }
                })
                .finally(() => setIsLoading(false))
        }
    }, [key, projectKey, workflowKey]);

    function RenderResult() {
        return data.map((data: any, index: number) => {
            let result = <span></span>;
            if (data.result) {
                result = <Tag color="blue">Pass</Tag>;
            } else {
                result = <Tag color="red">Faild</Tag>;
            }

            return <li key={index}>
                <TextStrong value={`Step ${data.index + 1}: `} />
                Kết quả: {result}
            </li>
        })
    }

    function RenderItems() {
        if (data.length > 0) {
            return data.map((data: any, index: number) => {
                let methodStr: string = "";

                switch (data.api.method) {
                    case 1:
                        methodStr = "Get";
                        break;
                    case 2:
                        methodStr = "Post";
                        break;
                    case 3:
                        methodStr = "Put";
                        break;
                    case 4:
                        methodStr = "Delete";
                        break;

                    default:
                        break;
                }

                let result = <span></span>;
                if (data.result) {
                    result = <Tag color="blue">Pass</Tag>;
                } else {
                    result = <Tag color="red">Faild</Tag>;
                }

                let resultPass = <span></span>;
                if (data.workflowDetail.resultPass) {
                    resultPass = <Tag color="blue">Pass</Tag>;
                } else {
                    resultPass = <Tag color="red">Faild</Tag>;
                }

                let body = data.api.body != null ? data.api.body : {};
                let res = data.response != null ? data.response : {};
                return <Box key={index}>
                    <h2 style={{ textDecoration: "underline" }}><TextStrong value={`Step: ${data.index + 1}`} /></h2>
                    <p><TextStrong value="Thứ tự: " /> {data.workflowDetail.orders}</p>
                    <p><TextStrong value={"Api Name:"} /> {data.api.name}</p>
                    <p><TextStrong value="Thời gian chờ: " /> {data.workflowDetail.timeDelay}</p>
                    <p><TextStrong value={<Tag color="blue">{methodStr}</Tag>} />{data.api.url}</p>
                    <p><TextStrong value="Pass khi kết quả: " /> {resultPass}</p>
                    <p><TextStrong value="Status code pass: " /> {data.workflowDetail.statusCodePass}</p>
                    <p><TextStrong value="Response pass: " /> {data.workflowDetail.responsesPass}</p>
                    <MonacoEditorComponent value={JSON.stringify(body)} />

                    <hr />
                    <h3 style={{ textDecoration: "underline" }}>Kết quả: </h3>
                    <p><TextStrong value={"Kết quả: "} />{result}</p>
                    <p><TextStrong value={"Status code: "} />{data.statusCode}</p>
                    <p><TextStrong value={"Thời gian: "} />{data.time}</p>
                    <MonacoEditorComponent value={JSON.stringify(res)} />
                </Box>
            });
        }
        return <span>Không có dữ liệu</span>;
    }

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail])

    function handleSubmitForm() {
        var body = {
            name: name
        };

        setIsLoading(true);
        if (key !== undefined) {
            edit(body);
        } else {
            add(body);
        }
    }

    function add(body: any) {
        WorkflowRunService.Add(body, projectKey, workflowKey)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                    nav(`/tools/tasks/projects/${projectKey}/workflows/${workflowKey}/run/${rs.data.key}/detail`);
                } else setIsLoading(false);
            })
    }

    function edit(body: any) {
        WorkflowRunService.GetDetail(key, projectKey, workflowKey)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestampV2(rs, body);
                    WorkflowRunService.Edit(key, body, projectKey, workflowKey)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                loadDataDetail();
                            } else setIsLoading(false);
                        })
                } else setIsLoading(false);
            })
    }

    function handleClickDelete() {
        Swal.fire({
            title: "Xác nhận xóa",
            text: "Bạn có muốn xóa không?"
        })
            .then((rs) => {
                if (rs.isConfirmed) {
                    doDelete();
                }
            })
    }

    function doDelete() {
        setIsLoading(true);
        WorkflowRunService.GetDetail(key, projectKey, workflowKey)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    WorkflowRunService.Delete(key, Fn.GetTimestampV2(rs), projectKey, workflowKey)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                nav(`/tools/tasks/projects/${projectKey}/workflows/${workflowKey}/run/`);
                            } else setIsLoading(false)
                        })
                } else setIsLoading(false)
            })
    }

    function handleClickExec() {
        setIsLoading(true);
        WorkflowRunService.ExecApi(key, projectKey, workflowKey)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setResDetail(JSON.stringify(rs.data));
                    loadDataDetail();
                } else setIsLoading(false);
            })
    }

    return (
        <MyBox
            title="Chi tiết workflow run"
            isLoading={isLoading}>
            <Box>
                <Form
                    layout="vertical"
                    onSubmitCapture={handleSubmitForm}>
                    <MyFormItem.Input
                        label="Tên"
                        value={name}
                        setValue={setName} />
                    <MyButton
                        type="success"
                        htmlType="submit"
                        value={key !== undefined ? "Chỉnh sửa" : "Thêm"} />

                    <MyButton
                        type="danger"
                        onClick={handleClickDelete}
                        disabled={key === undefined}
                        value="Xóa" />

                    <MyButton
                        type="primary"
                        onClick={handleClickExec}
                        disabled={key === undefined}
                        value="Chạy API" />
                </Form>
            </Box>
            <Box>
                <Row gutter={12}>
                    <Col span={12}>
                        <p>Result: </p>
                        <ul>
                            {RenderResult()}
                        </ul>
                    </Col>
                    <Col span={12}>
                        {/* <JsonViewerComponent data={resDetail}/> */}
                        <MonacoEditorComponent value={resDetail} />
                    </Col>
                </Row>
            </Box>
            {RenderItems()}
        </MyBox>
    );
}